div></div></div></div></div> <div data-aos=\"zoom-in\" data-aos-delay=\"300\" class=\"schedule-table tabPanel\"><div class=\"prog-list\"><div class=\"prog-item\"><div class=\"prog-row\"><div class=\"prog-col\"><div class=\"prog-img\"><div class=\"prog-img-pd\"><img width=\"1920\" height=\"1280\" src=\"/images/new-image/4.jpg\" alt data-lazy-srcset=\"https://ischool.vn/wp-content/uploads/2022/10/TDH_2662-scaled.jpg 1920w, https://ischool.vn/wp-content/uploads/2022/10/TDH_2662-300x200.jpg 300w, https://ischool.vn/wp-content/uploads/2022/10/TDH_2662-1024x683.jpg 1024w, https://ischool.vn/wp-content/uploads/2022/10/TDH_2662-768x512.jpg 768w, https://ischool.vn/wp-content/uploads/2022/10/TDH_2662-1536x1024.jpg 1536w, https://ischool.vn/wp-content/uploads/2022/10/TDH_2662-2048x1365.jpg 2048w, https://ischool.vn/wp-content/uploads/2022/10/TDH_2662-840x560.jpg 840w, https://ischool.vn/wp-content/uploads/2022/10/TDH_2662-50x33.jpg 50w, https://ischool.vn/wp-content/uploads/2022/10/TDH_2662-270x180.jpg 270w\" data-lazy-sizes=\"(max-width: 1920px) 100vw, 1920px\" data-lazy-src=\"https://ischool.vn/wp-content/uploads/2022/10/TDH_2662-scaled.jpg\" class=\"img\"></div></div></div> <div class=\"prog-col\"><div class=\"prog-content\"><h3 data-aos=\"fade-up-cus\" class=\"t-title prog-name mb-32\">Niềm tin</h3> <div data-aos=\"fade-up-cus\" data-aos-delay=\"300\" class=\"text mona-content\"><p style=\"text-align: justify\">Xây dựng niềm tin trong tập thể, học sinh, phụ huynh .</p></div></div></div></div></div></div></div> <div data-aos=\"zoom-in\" data-aos-delay=\"300\" class=\"schedule-table tabPanel\"><div class=\"prog-list\"><div class=\"prog-item\"><div class=\"prog-row\"><div class=\"prog-col\"><div class=\"prog-img\"><div class=\"prog-img-pd\"><img width=\"1920\" height=\"1280\" src=\"/images/new-image/6.jpg\" alt data-lazy-srcset=\"https://ischool.vn/wp-content/uploads/2022/10/TDH_4352-scaled.jpg 1920w, https://ischool.vn/wp-content/uploads/2022/10/TDH_4352-300x200.jpg 300w, https://ischool.vn/wp-content/uploads/2022/10/TDH_4352-1024x683.jpg 1024w, https://ischool.vn/wp-content/uploads/2022/10/TDH_4352-768x512.jpg 768w, https://ischool.vn/wp-content/uploads/2022/10/TDH_4352-1536x1024.jpg 1536w, https://ischool.vn/wp-content/uploads/2022/10/TDH_4352-2048x1365.jpg 2048w, https://ischool.vn/wp-content/uploads/2022/10/TDH_4352-840x560.jpg 840w, https://ischool.vn/wp-content/uploads/2022/10/TDH_4352-50x33.jpg 50w, https://ischool.vn/wp-content/uploads/2022/10/TDH_4352-270x180.jpg 270w\" data-lazy-sizes=\"(max-width: 1920px) 100vw, 1920px\" data-lazy-src=\"https://ischool.vn/wp-content/uploads/2022/10/TDH_4352-scaled.jpg\" class=\"img\"></div></div></div> <div class=\"prog-col\"><div class=\"prog-content\"><h3 data-aos=\"fade-up-cus\" class=\"t-title prog-name mb-32\">Chất lượng</h3> <div data-aos=\"fade-up-cus\" data-aos-delay=\"300\" class=\"text mona-content\"><p style=\"text-align: justify\">\n                            Luôn đặt chất lượng học tập của học sinh lên hằng đầu, làm kim chỉ nam cho các hoạt động cải thiện khoá học, dịch vụ\n                            khách hàng.\n                          </p></div></div></div></div></div></div></div> <div data-aos=\"zoom-in\" data-aos-delay=\"300\" class=\"schedule-table tabPanel\"><div class=\"prog-list\"><div class=\"prog-item\"><div class=\"prog-row\"><div class=\"prog-col\"><div class=\"prog-img\"><div class=\"prog-img-pd\"><img width=\"1920\" height=\"1280\" src=\"data:image/svg+xml,%3Csvg%20xmlns='http://www.w3.org/2000/svg'%20viewBox='0%200%201920%201280'%3E%3C/svg%3E\" alt data-lazy-srcset=\"https://ischool.vn/wp-content/uploads/2022/10/TDH_3325-scaled.jpg 1920w, https://ischool.vn/wp-content/uploads/2022/10/TDH_3325-300x200.jpg 300w, https://ischool.vn/wp-content/uploads/2022/10/TDH_3325-1024x683.jpg 1024w, https://ischool.vn/wp-content/uploads/2022/10/TDH_3325-768x512.jpg 768w, https://ischool.vn/wp-content/uploads/2022/10/TDH_3325-1536x1024.jpg 1536w, https://ischool.vn/wp-content/uploads/2022/10/TDH_3325-2048x1365.jpg 2048w, https://ischool.vn/wp-content/uploads/2022/10/TDH_3325-840x560.jpg 840w, https://ischool.vn/wp-content/uploads/2022/10/TDH_3325-50x33.jpg 50w, https://ischool.vn/wp-content/uploads/2022/10/TDH_3325-270x180.jpg 270w\" data-lazy-sizes=\"(max-width: 1920px) 100vw, 1920px\" data-lazy-src=\"https://ischool.vn/wp-content/uploads/2022/10/TDH_3325-scaled.jpg\" class=\"img\"></div></div></div> <div class=\"prog-col\"><div class=\"prog-content\"><h3 data-aos=\"fade-up-cus\" class=\"t-title prog-name mb-32\">Tôn Trọng</h3> <div data-aos=\"fade-up-cus\" data-aos-delay=\"300\" class=\"text mona-content\"><p style=\"text-align: left\">\n                            Tôn trọng bản thân và mọi người; chấp nhận người khác có những quan điểm không giống với mình. Trong nhà trường, “Tôn\n                            trọng” được thể hiện qua những phẩm chất và hành động:\n                          </p> <ul><li style=\"list-style-type: none\"><ul><li style=\"text-align: left\">Thông cảm, yêu thương và tôn trọng nhu cầu, tình cảm của người khác</li> <li style=\"text-align: left\">\n                                  Tôn trọng và thân thiện với sự khác biệt về hoàn cảnh, văn hóa và năng lực của mỗi cá nhân trong cộng đồng\n                                </li> <li style=\"text-align: left\">Tôn trọng sự khác biệt, ý kiến trái chiều, sự phản biện</li> <li style=\"text-align: left\">Tuân thủ nội quy của nhà trường</li> <li style=\"text-align: left\">Lắng nghe và không ngắt lời người khác</li> <li style=\"text-align: left\">Công nhận ưu điểm và khả năng của mỗi người</li> <li style=\"text-align: left\">Tôn trọng tính đa dạng về văn hóa trong cộng đồng nhà trường</li></ul></li></ul></div></div></div></div></div></div></div> <div data-aos=\"zoom-in\" data-aos-delay=\"300\" class=\"schedule-table tabPanel\"><div class=\"prog-list\"><div class=\"prog-item\"><div class=\"prog-row\"><div class=\"prog-col\"><div class=\"prog-img\"><div class=\"prog-img-pd\